<template>
<div class="homePageTop" :style="{ background: background,color:color}">
  {{ company }}
</div>
</template>

<script>
export default {
  data() {
    return {}
  },
  props: {
    company: {
      type: String,
      default: '-  -'
    },
    background: {
      type: String,
      default: '#fff'
    },
    color: {
      type: String,
      default: '#000'
    }
  }
}
</script>

<style lang="scss" scoped>
.homePageTop {
    margin-top: 10px;
    text-align: center;
    padding: 15px;
    background: #fff;
    font-size: 16px;
}
</style>



